import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import xiaoyao from '@/components/xiaoyao'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/xiaoyao',
      name: 'xiaoyao',
      component: xiaoyao
    }
  ]
})



